.alpha10 {
    background: rgb(0, 0, 0);
    background: transparent \9;
    background: rgba(0, 0, 0, 0.1);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#19000000, endColorstr=#19000000);
    zoom: 1;
}

.alpha10:nth-child(n) {
    filter: none;
}

